import { RouterModule, Routes } from '@angular/router';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatToolbarModule } from '@angular/material/toolbar'
import { MatTableModule } from '@angular/material/table';
import { MatButtonModule } from '@angular/material/button';
import { MatSelectModule } from '@angular/material/select';
import { MatFormFieldModule } from '@angular/material/form-field';
import { ReactiveFormsModule } from '@angular/forms';
import { MatInputModule } from '@angular/material/input';
import { FuseAlertComponent } from '@fuse/components/alert';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { MatIconModule } from '@angular/material/icon';
import { AppAlertService } from '@shared/service/app/alert.service';
import { MatTooltipModule } from '@angular/material/tooltip';
import { DepositosComponent } from './components/depositos.component';
import { SaveComponent } from './components/save/save.component';
import { DepositosService } from './service/depositos.service';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatSortModule } from '@angular/material/sort';
import { TablaDinamicaModule } from '@shared/components/tabla-dinamica/tabla-dinamica.module';
import { FilterMenuComponent } from './components/filter-menu/filter-menu.component';
import { StockDepositoComponent } from './components/stock-deposito/stock-deposito.component';
// import { PermisoGuard } from '@core/auth/guards/permiso.guard';
import { MatPaginatorModule } from '@angular/material/paginator';
import { FilterStockDepositoComponent } from './components/filter-stock-deposito/filter-stock-deposito.component';
const routes: Routes = [
    {
        path: '',
        component: DepositosComponent
    },
    {
        path: ':id',
        component: StockDepositoComponent,
        data: { title: 'Stock por depósito' },
        // canActivate: [PermisoGuard],
    }
];

@NgModule({
    declarations: [DepositosComponent, SaveComponent, FilterMenuComponent, StockDepositoComponent, FilterStockDepositoComponent],
    imports: [
        CommonModule,
        RouterModule.forChild(routes),
        MatTableModule,
        MatButtonModule,
        MatSelectModule,
        MatFormFieldModule,
        ReactiveFormsModule,
        MatInputModule,
        MatIconModule,
        FuseAlertComponent,
        MatTooltipModule,
        MatAutocompleteModule,
        MatToolbarModule,
        MatCheckboxModule,
        MatSortModule,
        TablaDinamicaModule,
        MatPaginatorModule
    ],
    exports: [RouterModule],
    providers: [DepositosService, JedsMenuService, AppAlertService]
})
export class DepositosModule { }