import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { fuseAnimations } from '@fuse/animations';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { IRubro } from 'app/modules/stock/rubros/models/rubro';
import { firstValueFrom, Subject, takeUntil } from 'rxjs';
import { DepositosService } from '../../service/depositos.service';

@Component({
    selector: 'app-filter-stock-deposito',
    templateUrl: './filter-stock-deposito.component.html',
    styleUrls: ['./filter-stock-deposito.component.scss'],
    animations: fuseAnimations
})
export class FilterStockDepositoComponent implements OnInit, OnDestroy {
    public formFilters: FormGroup;
    filterRubro: IRubro[] = [];
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    constructor(
        @Inject('MENU_DATA') public data: any,
        private menuService: JedsMenuService,
        private _empresaService: EmpresaService,
        private _depositosService: DepositosService
    ) { }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.formFilters = new FormGroup({
            tipo: new FormControl(-1),
            rubro: new FormControl(-1)
        });
        this.formFilters.patchValue(this.data.filter);

        this.getRubros();
    }

    resetFilters() {
        this.formFilters.reset({
            tipo: -1,
            rubro: -1
        });
    }

    onCloseMenu(data: any = null) {
        this.menuService.close(data);
    }

    /**
    * Esta funcion es para listas los Rubros en el filtro de grilla.
    */
    async getRubros() {
        const rubros = await firstValueFrom(this._depositosService.getRubros());
        this.filterRubro = rubros.list;
    }

    // Este método es llamado cuando el componente es destruido
    ngOnDestroy(): void {
        // Emite un valor para cerrar todas las suscripciones
        this._unsubscribeAll.next(null);
        this._unsubscribeAll.complete();
    }
}